.App {
  max-width: 860px;
  margin: 10px auto;
}
h1 {
  transition: all .3s ease-in-out;
  margin: 0 0 80px;
  font-size: 2.5rem;
}
.game-settings {
  transition: all .3s ease-in-out;
  margin: 0 auto;
  width: fit-content;
  padding: 1rem;
  border: 2px solid;
  border-radius: 1rem;
  background-color: #2c1e3e;
}
button {
  background: #c23866;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
  box-shadow: 0 3px 3px rgba(255, 255, 255, 0.175);
}
button:hover {
  background: none;
  color: #fff;
}
.card-grid {
  margin: 40px 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 2rem;
}

.card-grid-medium {
  margin: 40px 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 1.5rem;
}

.card-grid-hard {
  margin: 40px 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 1rem;
}

.select-difficulty {
  margin: 20px 0 0;
  font-family: Raleway, sans-serif;
  padding: 5px;
  font-size: 1.5rem;
  background-color: #2c1e3e;
  color: white;
  border-radius: 10px;
  outline: none;
}

.turns {
  background-color: #fff;
  padding: .25rem 1rem;
  border: 3px solid #c23866;
  z-index: 10;
  
  border-bottom-left-radius: 16px;
  border-top-left-radius: 16px;
  color: black;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 2.5rem;
  position: relative;
  float: right;
  top: 0;
  right: 0;
}

.turns img {
  width: 40px;
  margin-right: 5px;
}

.settings {
  display: none;
}

.mobile {
  display: none;
}

@media screen and (max-width: 600px){
  .card-grid {
    margin: 20px 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: .5rem;
  }

  .card-grid-medium {
    margin: 20px 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: .5rem;
  }
  
  .card-grid-hard {
    margin: 20px 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: .5rem;
  }

  .mobile {
    display: flex;
  }

  .desktop {
    display: none;
  }

  .turns {
    transition: all .3s ease-in-out;
    opacity: 70%;
    position: fixed;
    top: 70px;
    border-right: 0;
  }

  .settings {
    transition: all .3s ease-in-out;
    background-color: #fff;
    padding: .25rem 1rem;
    border: 3px solid #c23866;
    opacity: 70%;
    border-left: 0;
    z-index: 10;
    display: flex;
    align-items: center;
    border-bottom-right-radius: 16px;
    border-top-right-radius: 16px;
    color: black;
    font-size: 2.5rem;
    position: fixed;
    top: 70px;
    left: 0;
  }

}